import React, { useState } from "react";
import NumberFormat from "react-number-format";
// import _ from "lodash";

const ComponentDetailElementList = (props) => {
  return <Wrapper2 myRef={props.myRef} {...props}></Wrapper2>;
};

const Wrapper1 = (props) => {
  return (
    <td
      className="dfkosdkjspdjsksadsdsasr"
      style={{ display: "flex", alignItems: "center" }}
    >
      <Logo {...props}></Logo>
      <Name {...props}></Name>
      {/* <Symbol {...props}></Symbol> */}
    </td>
  );
};

const Logo = (props) => {
  return (
    <span
      className="fjdaklisfjlkassdasajiorejas"
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: 24,
        height: 24,
        marginRight: 8,
      }}
    >
      <img
        src={
          "https://quantifycrypto.s3-us-west-2.amazonaws.com/pictures/crypto-img/32/icon/" +
          props?.qc_key?.toLowerCase().trim() +
          ".png"
        }
        alt="open"
        style={{
          width: "100%",
          height: "100%",
          objectFit: "cover",
          borderRadius: "50%",
        }}
      />
    </span>
  );
};

const Name = (props) => {
  return (
    <span
      title={props.qc_key}
      className="jvkopsjfrkesasasarswoeeqa0"
      style={{
        justifyContent: "center",
        alignItems: "center",
        fontSize: 14,
        fontWeight: 600,
        marginRight: 8,
      }}
    >
      {props.qc_key}
    </span>
  );
};

const Trend = (props) => {
  const [qma_score, setQma_score] = useState(() => props.qma_score);
  return (
    <div
      className={`assajkshaksasasa ${
        parseInt(qma_score) >= 75 ? "dhgsjdhgajakssaas" : ""
      } ${parseInt(qma_score) < 50 ? "dawyuawhauksgfsere" : ""} ${
        parseInt(qma_score) >= 50 && parseInt(qma_score) < 75
          ? "dehasajkhaskjsajksa"
          : ""
      }`}
      style={{
        justifyContent: "center",
        alignItems: "center",
        fontSize: 14,
        fontWeight: 600,
        marginRight: 8,
      }}
    >
      {qma_score}%
    </div>
  );
};

const Daily = (props) => {
  const [price24h, setPrice24h] = useState(() => props.price24h);
  return (
    <div
      className={`assajkshaksasasa ${
        parseFloat(price24h) < 0 ? "dawyuawhauksgfsere" : ""
      } ${parseFloat(price24h) >= 0 ? "dhgsjdhgajakssaas" : ""} `}
      style={{
        justifyContent: "center",
        alignItems: "center",
        fontSize: 14,
        fontWeight: 600,
        marginRight: 8,
      }}
    >
      {(price24h * 100).toFixed(2)} %
    </div>
  );
};

