const Wrapper2 = (props) => {
  return (
    <>
      <Wrapper1 {...props}></Wrapper1>
      <Price myRef={props.myRef} {...props}></Price>
      <Daily {...props}></Daily>
      <Trend {...props}></Trend>
      {/* <TwentyFourHours {...props}></TwentyFourHours> */}
      {/* <MarketCap {...props}></MarketCap> */}
      {/* <Volume24h {...props}></Volume24h> */}
    </>
  );
};

const Price = (props) => {
  const [price, setPrice] = useState(() => props.price_usd);

  return (
    <td
      className="fksledjkijfsaaaedikdsaassdwqa"
      style={{
        display: "flex",
        justifyContent: "end",
        alignItems: "center",
        fontWeight: 600,
      }}
    >
      
      <NumberFormat
        prefix="$ "
        renderText={(value, props) => (
          <span style={{ fontSize: 14 }}>{value}</span>
        )}
        decimalSeparator="."
        displayType={"text"}
        thousandSeparator={true}
        value={price}
      ></NumberFormat>
      {/* <div id="dsjkjklfslkjsddsaw" ref={props.myRef}></div> */}
      {/* {
        console.log(document.querySelector("#dsjkjklfslkjsddsaw")?.innerText)
      } */}
    </td>
  );
};

